
const changeOpacity = (el?:HTMLElement) => {
    console.log(el)
    return function (){
        //console.log($event,el)
        if(el){
            const scrollTop = document.documentElement.scrollTop | document.body.scrollTop
            if(scrollTop !== 0){
                el.style.opacity = `${1000/scrollTop * 0.02 > 0.1 ? 1000/scrollTop * 0.02  : 0}`
            }else{
                el.style.opacity = 1 + ''
            }
        }else{
            return
        }

    }
}


